
"use client";

import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { detailedPatients, patientManager, Patient } from "@/lib/constants";
import { Button } from "@/components/ui/button";
import Link from "next/link";
import { ArrowRight, MoreHorizontal } from "lucide-react";
import { DropdownMenu, DropdownMenuContent, DropdownMenuItem, DropdownMenuTrigger } from "@/components/ui/dropdown-menu";
import { useState, useEffect } from "react";
import { useToast } from "@/hooks/use-toast";


const appointments = [
    { patient: 'Michael Brown', time: '10:00 AM', reason: 'Follow-up' },
    { patient: 'Emily Davis', time: '11:30 AM', reason: 'New Consultation' },
    { patient: 'Chris Wilson', time: '02:00 PM', reason: 'Routine Checkup' },
];

export default function DoctorDashboard() {
  const [patients, setPatients] = useState(patientManager.getPatients());
  const { toast } = useToast();

  useEffect(() => {
    const handleUpdate = () => {
        setPatients([...patientManager.getPatients()]);
    };
    const unsubscribe = patientManager.subscribe(handleUpdate);
    return () => unsubscribe();
  }, []);

  const handleConditionChange = (patientId: string, condition: string) => {
    patientManager.updatePatientCondition(patientId, condition);
    toast({
        title: "Patient Condition Updated",
        description: `The patient's condition has been set to ${condition}.`
    });
  };

  const myPatients = patients.slice(0, 4);

  return (
    <div className="grid gap-6 md:grid-cols-1 lg:grid-cols-2">
      <Card>
        <CardHeader>
          <CardTitle>My Patients</CardTitle>
          <CardDescription>A list of your currently admitted patients.</CardDescription>
        </CardHeader>
        <CardContent>
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>Name</TableHead>
                <TableHead>Condition</TableHead>
                <TableHead><span className="sr-only">Actions</span></TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {myPatients.map((patient) => (
                <TableRow key={patient.id}>
                  <TableCell className="font-medium flex items-center gap-3">
                    <Avatar>
                      <AvatarImage src={`https://placehold.co/40x40.png?text=${patient.name.charAt(0)}`} alt={patient.name} data-ai-hint="person" />
                      <AvatarFallback>{patient.name.charAt(0)}</AvatarFallback>
                    </Avatar>
                    {patient.name}
                  </TableCell>
                  <TableCell>
                    <DropdownMenu>
                      <DropdownMenuTrigger asChild>
                         <Badge variant={patient.condition === 'Critical' ? 'destructive' : patient.condition === 'Normal' ? 'default' : 'secondary'} className="cursor-pointer">
                            {patient.condition}
                         </Badge>
                      </DropdownMenuTrigger>
                      <DropdownMenuContent>
                        <DropdownMenuItem onClick={() => handleConditionChange(patient.id, 'Critical')}>Critical</DropdownMenuItem>
                        <DropdownMenuItem onClick={() => handleConditionChange(patient.id, 'Improving')}>Improving</DropdownMenuItem>
                        <DropdownMenuItem onClick={() => handleConditionChange(patient.id, 'Stable')}>Stable</DropdownMenuItem>
                        <DropdownMenuItem onClick={() => handleConditionChange(patient.id, 'Normal')}>Normal</DropdownMenuItem>
                      </DropdownMenuContent>
                    </DropdownMenu>
                  </TableCell>
                  <TableCell className="text-right">
                    <Button asChild variant="outline" size="sm">
                       <Link href={`/doctor/patients/${patient.id}`}>
                          View
                          <ArrowRight className="ml-2 h-4 w-4" />
                       </Link>
                    </Button>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </CardContent>
      </Card>
      <Card>
        <CardHeader>
          <CardTitle>Today's Appointments</CardTitle>
           <CardDescription>Your schedule for today.</CardDescription>
        </CardHeader>
        <CardContent>
           <Table>
            <TableHeader>
              <TableRow>
                <TableHead>Patient</TableHead>
                <TableHead>Time</TableHead>
                <TableHead>Reason</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {appointments.map((appt) => (
                <TableRow key={appt.patient}>
                  <TableCell className="font-medium">{appt.patient}</TableCell>
                  <TableCell>{appt.time}</TableCell>
                  <TableCell>{appt.reason}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </CardContent>
      </Card>
    </div>
  );
}
